import {
  Backdrop,
  Button,
  Grid,
  IconButton,
  makeStyles,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import React, { useCallback, useState } from "react";
import MenuIcon from "@material-ui/icons/Menu";

import { HashLink } from "react-router-hash-link";
import { CloseOutlined } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  navigationContainer: {},
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: "#fff",
    backgroundColor: "#000000f1",
  },
}));

interface NavItem {
  id: string;
  route: string;
}

function Navigation() {
  const classes = useStyles();
  const theme = useTheme();
  const enoughSpace = useMediaQuery(theme.breakpoints.up("sm"));
  const [drawerVisible, setDrawerVisible] = useState(false);
  const showNavigationDrawer = useCallback(() => {
    setDrawerVisible(true);
  }, []);
  const closeBackdrop = () => {
    setDrawerVisible(false);
  };
  if (enoughSpace) {
    return (
      <Grid
        className={classes.navigationContainer}
        container
        item
        justify="flex-end"
        alignItems="flex-end"
        xs={10}
      >
        <Grid item>
          <Button color="primary" component={HashLink} smooth to="#about">
            HAKKIMIZDA
          </Button>
        </Grid>
        <Grid item>
          <Button color="primary" component={HashLink} smooth to="#education">
            EĞİTİM
          </Button>
        </Grid>
        <Grid item>
          <Button color="primary" component={HashLink} smooth to="#contact">
            İLETİŞİM
          </Button>
        </Grid>
      </Grid>
    );
  } else {
    return (
      <>
        <Grid
          className={classes.navigationContainer}
          container
          item
          justify="flex-end"
          alignItems="flex-end"
          xs={10}
        >
          <IconButton onClick={showNavigationDrawer}>
            <MenuIcon></MenuIcon>
          </IconButton>
        </Grid>
        <Backdrop open={drawerVisible} className={classes.backdrop}>
          <IconButton
            style={{
              position: "fixed",
              right: "16px",
              top: "10px",
              color: "#fff",
            }}
            onClick={closeBackdrop}
          >
            <CloseOutlined></CloseOutlined>
          </IconButton>
          <Grid
            className={classes.navigationContainer}
            direction="column"
            container
            item
            justify="center"
            alignItems="center"
            xs={12}
          >
            <Grid item>
              <Button
                onClick={closeBackdrop}
                color="primary"
                component={HashLink}
                smooth
                to="#about"
              >
                HAKKIMIZDA
              </Button>
            </Grid>
            <Grid item>
              <Button
                onClick={closeBackdrop}
                color="primary"
                component={HashLink}
                smooth
                to="#education"
              >
                EĞİTİM
              </Button>
            </Grid>
            <Grid item>
              <Button
                onClick={closeBackdrop}
                color="primary"
                component={HashLink}
                smooth
                to="#contact"
              >
                İLETİŞİM
              </Button>
            </Grid>
          </Grid>
        </Backdrop>
      </>
    );
  }
}

export default Navigation;
