import { Grid, Typography } from "@material-ui/core";
import React from "react";
import { use100vh } from "react-div-100vh";
import logo from "../naek_logo.svg";

function Welcome() {
  const height = use100vh();
  const heightWithoutTopBar = height ? height - 60 : "calc(100vh - 60px)";
  return (
    <Grid
      justify="center"
      alignItems="center"
      direction="column"
      container
      style={{ height: heightWithoutTopBar }}
    >
      <Grid item style={{width: "100%"}} >
        <img src={logo} style={{ width: "100%", maxHeight: "300px" }} alt="logo" />
      </Grid>
      <Grid style={{padding: "32px"}} container item alignItems="center" justify="center">
        <Grid item>
          <Typography color="primary" variant="h5">
            ileri düzey genel kültür eğitimi...
          </Typography>
        </Grid>
      </Grid>
    </Grid>
  );
}

export default Welcome;
