import React, { useEffect, useState } from "react";
import "./App.css";
import NavBar from "./components/Navbar/NavBar";
import {
  Container,
  createMuiTheme,
  CssBaseline,
  ThemeProvider,
  responsiveFontSizes,
} from "@material-ui/core";
import About from "./components/About";
import Education from "./components/Education";
import Contact from "./components/Contact";
import Welcome from "./components/Welcome";
import { BrowserRouter } from "react-router-dom";
import brown from "@material-ui/core/colors/brown";
import Fab from "@material-ui/core/Fab";
import UpIcon from "@material-ui/icons/KeyboardArrowUp";
import { use100vh, measureHeight } from "react-div-100vh";

let theme = createMuiTheme({
  palette: {
    primary: {
      main: brown[500],
    },
    secondary: {
      main: "#DFC83B",
    },
  },
});

theme = responsiveFontSizes(theme);

function App() {
  const scrollToTop = () => {
    window["scrollTo"]({ top: 0, behavior: "smooth" });
  };
  const [showScrollToTop, setShowScrollToTop] = useState(false);
  const [clientHeight, setClientHeight] = useState<number>();

  use100vh();

  const heightChanged = () => {
    const currentHeight = measureHeight();
    if (currentHeight) {
      setClientHeight(currentHeight);
    }
  };

  const handleScroll = () => {
    if (clientHeight) {
      if (window.pageYOffset > clientHeight) {
        setShowScrollToTop(true);
      } else {
        setShowScrollToTop(false);
      }
    }
  };

  useEffect(() => {
    handleScroll();
  }, [clientHeight]);

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    window.addEventListener("resize", heightChanged);
    const currentHeight = measureHeight();
    if (currentHeight) {
      setClientHeight(currentHeight);
    }
    return () => {
      window.removeEventListener("scroll", handleScroll);
      window.removeEventListener("resize", heightChanged);
    };
  });

  return (
    <BrowserRouter>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Container>
          <NavBar></NavBar>
          <Welcome></Welcome>
          <About></About>
          <Education></Education>
          <Contact></Contact>
          {showScrollToTop && (
            <Fab
              size="small"
              onClick={scrollToTop}
              style={{ position: "fixed", right: "16px", bottom: "16px" }}
            >
              <UpIcon></UpIcon>
            </Fab>
          )}
        </Container>
      </ThemeProvider>
    </BrowserRouter>
  );
}

export default App;
